<template>
  <div class="wrapper">
    <Header></Header>
    <div class='child-view'>
    <transition>
      <router-view />
    </transition>
    </div>
    <div class='footer'>
      <div class="tab-wrapper">
        <div class="tab clearfloat">
          <div class="tab-item" v-for="(item,index) in routerData" :key="index">
            <router-link :to="{path:item.link,query:{name:headerName}}" tag="span" active-class="tab-active" v-html="item.eachName">
            </router-link>
          </div>
        </div>
        <div class='call-back' @click='callBack'></div>
      </div>
    </div>
  </div>
</template>

<script>
  import star from '@/components/star/index'
  import Header from '@/components/Header/index'
  export default {
    name: 'index',
    components: {
      Header,
      star
    },
    data() {
      return {
        // routerIndex: 0,
        headerName:'',
        routerData: [
          {link: "/zdxfryfb/zlfx", eachName: "总量分析"}
        ]
      }
    },
    mounted() {
      this.headerName = this.$route.query.name
      this.autoRem();
      window.onresize = () => {
        this.autoRem();
      }
    },
    methods: {
      autoRem() {
        let scale = document.body.clientWidth / 1920;
        document.getElementsByTagName('html')[0].style.fontSize = scale * 100 + 'px';
      },
      callBack(){
        this.$router.push({
          path:'/',
          name:'index'
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
.wrapper
	overflow visible
	.child-view
		position absolute
		top 11vh
		left 0
		z-index 2
		width 100%
		height calc(100% - 215px)
		transition all 1s
		box-sizing border-box
		overflow hidden
	.footer
		position fixed
		left 0
		bottom 0
		z-index 2
		width 100%
		height 90px
		background url("../../assets/footer-bg.png") no-repeat center
		background-size 100% 100%
	.tab-wrapper 
		position relative
		width 100%
		height 100%
		.call-back
			width 35px 
			height 35px
			background url("../../assets/call-back.png") no-repeat center
			background-size 100% 100%
			position absolute
			top 0.42rem
			right .8rem
			cursor pointer
		.tab
			position absolute
			left 2%
			bottom 0
			display flex
			width 96%
			height 0.66rem
			.tab-item
				padding 0.18rem 0rem
				cursor pointer
				text-align center
				span 
					display block
					font-size 0.24rem
					color #cbfbff
					padding 0rem 0.7rem
					line-height 0.3rem
					border-right 1px solid #cbfbff
			.tab-active
				color #fff
				font-weight 700
				text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
    .v-enter, v-leave
      opacity 0
    .v-enter-active
      transition opacity 1s
    .v-enter-to
			opacity 1

</style>

